import React from "react";
import { useForm } from "react-hook-form";

function AnonymityComp({ onSubmit }) {
  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm();

  return (
    <>
      <div className="page_title">익명의 소리</div>
      <div className="page_subtitle">반 내에 불만사항 및 개선사항을 적어주세요.</div>
      <form onSubmit={handleSubmit(onSubmit)} className={"container mx-auto"}>
        <textarea
          {...register("mailContent", { required: true, minLength: 20 })}
          name="mailContent"
          className="input_anony resize-none"
          placeholder="최소 20글자 이상"
        ></textarea>
        {errors.mailContent?.type === "required" && (
          <div className="validation-error">필수 항목 입니다.</div>
        )}
        {errors.mailContent?.type === "minLength" && (
          <div className="validation-error">20글자 이상 적으세요.</div>
        )}
        <input type="submit" value="보내기" className={"confirm-btn"} />
      </form>
    </>
  );
}

export default AnonymityComp;
