import React from "react";
import AnonymityComp from "../../../main/anonymity/AnonymityComp";
import fetcher from "../../../../utils/fetcher-utils";
import { toast } from "react-toastify";
import { useNavigate } from "react-router";

function AnonymityPage() {
  const navigate = useNavigate();
  const onSubmit = (data) => {
    fetcher.post("/anonymity", data).then(() => {
      toast.success("익명소리함을 작성했습니다.");
      navigate("/");
    });
  };

  return <AnonymityComp onSubmit={onSubmit} />;
}

export default AnonymityPage;
